* {
	margin:0;
	padding:0;
	box-sizing: border-box;
}

body {

	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: url(https://sapagroup.net/wp-content/uploads/2018/12/how-to-grow-in-the-automotive-sector-sapa-received-the-elite-certificate-of-the-italian-stock-exchange_01.jpg) no-repeat ;
	background-size: auto
	;

 /* background: url(https://cryptotrek.ru/wp-content/uploads/2020/03/haccc.jpg)   no-repeat ;
	background-size: 100%;  */
}  

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
a {
	text-decoration: none;
	color: white;
	
}
a.header > * {
	color: #00BFFF !important;
	border-bottom: 4px solid #00BFFF;
	
}

a:hover {
	color: #00BFFF;
}
a:hover > * {
	cursor: pointer;
	
}

/* Let's get this party started */
::-webkit-scrollbar {
	width: 7px;

}
/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #202225; 
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
::-webkit-scrollbar-thumb:window-inactive {
	background: #2e3338; 
}